/* General form styles */
section h3 {
    font-family: 'vazirmatn', sans-serif;
  }
  form label {
    font-size: 1rem;
    font-weight: bold;
  }
  
  form input {
    font-size: 1rem;
    padding: 0.5rem;
  }
  
  /* Mobile-first layout */
  .container {
    padding: 1rem;
  }
  
  h3 {
    font: 'vazirmatn';
    font-size: 1.2rem;
    text-align: center;
  }
  
  button {
    width: 100%; /* Full width for mobile screens */
    padding: 0.8rem;
    font-size: 1rem;
    font-weight: bold;
  }
  
  /* Larger screens (tablet and up) */
  @media (min-width: 768px) {
    h3 {
      font-size: 1.5rem;
    }
  
    button {
      width: auto; /* Auto width for larger screens */
    }
  }
  